import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/pages/Home'
import Dashboard from '@/pages/Dashboard'
import BasicTable from '@/pages/BasicTable'
import EditableTable from '@/pages/EditableTable'
import Widget from '@/pages/Widget'
import Panels from '@/pages/Panels'
import Editor from '@/pages/Editor'
import ImageList from '@/pages/ImageList'
import Charts from '@/pages/Charts'
import Login from '@/pages/Login'
import LockScreen from '@/pages/LockScreen'
import Loading from '@/pages/Loading'
import Notes from '@/pages/Notes'
import BookInfo from '@/pages/BookInfo'
import AddBook from '@/pages/AddBook'
import AddNote from '@/pages/AddNote'
//import OwnSpace from '@pages/OwnSpace'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      component: Home,
      children: [
        {
          path: '/',
          name: 'Dashboard',
          component: Dashboard
        },
        {
          path: '/bookInfo',
          name: 'BookInfo',
          component: BookInfo
        },
        // {
        //   path: '/ownSpace',
        //   name: 'OwnSpace',
        //   component: OwnSpace
        // },
        {
          path: '/addNote',
          name: 'AddNote',
          component: AddNote
        },
        {
          path: '/addBook',
          name: 'AddBook',
          component: AddBook
        },
        {
          path: 'widget',
          name: 'Widget',
          component: Widget
        },
        {
          path: 'panels',
          name: 'Panels',
          component: Panels
        },
        {
          path: 'notes',
          name: 'Notes',
          component: Notes
        },
        {
          path: 'editor',
          name: 'Editor',
          component: Editor
        },
        {
          path: 'imagelist',
          name: 'ImageList',
          component: ImageList
        },
        {
          path: 'basic-table',
          name: 'BasicTable',
          component: BasicTable
        },
        {
          path: 'editable-table',
          name: 'EditableTable',
          component: EditableTable
        },
        {
          path: 'charts',
          name: 'Charts',
          component: Charts
        }
      ]
    },
    {
      path: '/login',
      name: 'Login',
      components: {
        blank: Login
      }
    },
    {
      path: '/lockscreen',
      name: 'Lockscreen',
      components: {
        blank: LockScreen
      }
    },
    {
      path: '/loading',
      name: 'Loading',
      components: {
        blank: Loading
      }
    }
  ]
})



