<template>
    <Form :model="formItem" :label-width="80">
        <FormItem label="TITLE">
            <Input v-model="formItem.input" placeholder="Enter something..."></Input>
        </FormItem>
        <FormItem label="FROM">
            <DatePicker type="datetime" placeholder="Select date and time" style="width: 408px"></DatePicker>
        </FormItem>
        <FormItem label="TO">
            <DatePicker type="datetime" placeholder="Select date and time" style="width: 408px"></DatePicker>
        </FormItem>
        <FormItem label="CONTEXT">
            <Input v-model="formItem.textarea" type="textarea" :autosize="{minRows: 2,maxRows: 5}" placeholder="Enter something..."></Input>
        </FormItem>
        <FormItem label="VISIBLE">
            <i-switch v-model="formItem.switch" size="large">
                <span slot="open">yes</span>
                <span slot="close">no</span>
            </i-switch>
        </FormItem>
    </Form>
</template>
<script>
    export default {
        name: 'VmFormNote',
        data () {
            return {
                formItem: {
                    input: '',
                    select: '',
                    radio: 'male',
                    checkbox: [],
                    switch: true,
                    date: '',
                    time: '',
                    slider: [20, 50],
                    textarea: ''
                }
            }
        }
    }
</script>


