<template>
    <div class="vm-image-list">
    </br><h1>Add Book</h1></br>
    <Form ref="formValidate" :model="formValidate" :rules="ruleValidate" :label-width="80">
        <FormItem label="Title" prop="title">
            <Input v-model="formValidate.title" placeholder="Enter title"></Input>
        </FormItem>
        <FormItem label="Subtitle" prop="subtitle">
            <Input v-model="formValidate.subtitle" placeholder="Enter subtitle"></Input>
        </FormItem>
        <FormItem label="Author" prop="author">
            <Input v-model="formValidate.author" placeholder="Enter author"></Input>
        </FormItem>
        <FormItem label="Translator" prop="translator">
            <Input v-model="formValidate.translator" placeholder="Enter translator"></Input>
        </FormItem>
        <FormItem label="Press" prop="press">
            <Input v-model="formValidate.press" placeholder="Enter press"></Input>
        </FormItem>
        <FormItem label="Publish Date" prop="date">
            <DatePicker type="date" placeholder="Select date" v-model="formValidate.date"></DatePicker>
        </FormItem>
        <FormItem label="TotalPage" prop="totalPage">
            <InputNumber :min="1" v-model="formValidate.totalPage"></InputNumber>
        </FormItem>
        <FormItem label="Upload Image" prop="img">
            <Upload
                ref="upload" name="upfile"
                :show-upload-list="false"
                :default-file-list="defaultList"
                :on-success="handleSuccess"
                :format="['jpg','jpeg','png']"
                :max-size="2048"
                :on-format-error="handleFormatError"
                :on-exceeded-size="handleMaxSize"
                :before-upload="handleBeforeUpload"
                multiple
                type="drag"
                action="http://localhost:8090/uploadImg"
                style="display: inline-block;width:58px;">
                <div style="width: 58px;height:58px;line-height: 58px;">
                    <Icon type="camera" size="20"></Icon>
                </div>
            </Upload>
            <!--<Upload
                multiple
                type="drag"
                action="//jsonplaceholder.typicode.com/posts/">
                <div style="padding: 20px 0">
                    <Icon type="ios-cloud-upload" size="52" style="color: #3399ff"></Icon>
                    <p>Click or drag images here to upload</p>
                </div>
            </Upload>
            <VmUploadImg :uploadList="defaultList"></VmUploadImg>-->
        </FormItem>
        <FormItem label="Intro" prop="desc">
            <Input v-model="formValidate.desc" type="textarea" :autosize="{minRows: 2,maxRows: 5}" placeholder="Enter something..."></Input>
        </FormItem>
        <FormItem>
            <Button type="primary" @click="handleSubmit('formValidate')">Submit</Button>
            <Button type="ghost" @click="handleReset('formValidate')" style="margin-left: 8px">Reset</Button>
        </FormItem>
    </Form>
    </div>
</template>
<script>
    //import VmUploadImg from '@/components/vm-upload-img'
    export default {
        name: 'AddBook',
        data () {
            return {
                defaultList: [
                    {
                        'name': '1984.jpg',
                        'url': '@/assets/img/1984.jpg'
                    }
                ],
                imgName: '',
                visible: false,
                uploadList: [],
                formValidate: {
                    title: '',
                    subtitle: '',
                    author: '',
                    translator: '',
                    press: '',
                    date: '',
                    desc: '',
                    totalPage: 1
                },
                ruleValidate: {
                    title: [
                        { required: true, message: 'The title cannot be empty', trigger: 'blur' }
                    ],
                    press: [
                        { required: true, message: 'The press cannot be empty', trigger: 'blur' }
                    ],
                    totalPage: [
                        { required: true, message: 'Total page cannot be empty', trigger: 'blur' },
                        { type: 'number', message: 'Incorrect number format', trigger: 'blur' }
                    ],
                    // subtitle: [
                    //     { required: true, message: 'Mailbox cannot be empty', trigger: 'blur' },
                    //     { type: 'email', message: 'Incorrect email format', trigger: 'blur' }
                    // ],
                    author: [
                        { required: true, message: 'The author cannot be empty', trigger: 'blur' }
                    ],

                    // interest: [
                    //     { required: true, type: 'array', min: 1, message: 'Choose at least one hobby', trigger: 'change' },
                    //     { type: 'array', max: 2, message: 'Choose two hobbies at best', trigger: 'change' }
                    // ],
                    date: [
                        { required: true, type: 'date', message: 'Please select the date', trigger: 'change' }
                    ],
                    desc: [
                        { required: true, message: 'Please enter a personal introduction', trigger: 'blur' },
                        { type: 'string', min: 20, message: 'Introduce no less than 20 words', trigger: 'blur' }
                    ]
                }
            }
        },
        methods: {
            handleSubmit (name) {
                let param = new URLSearchParams();
                param.append("title", this.formValidate.title);
                param.append("subtitle", this.formValidate.subtitle);
                param.append("author", this.formValidate.author);
                param.append("translator", this.formValidate.translator);
                param.append("press", this.formValidate.press);
                param.append("date", this.formValidate.date);
                param.append("desc", this.formValidate.desc);
                param.append("totalPage", this.formValidate.totalPage);
                
                
                this.$refs[name].validate((valid) => {
                    if (valid) {
                        this.$axios.post('fiction-note/loginUser', loginParam)
                        .then(function(response) {
                            // 这里是处理正确的回调
                            if (100 == response.data.code) {
                            
                            } else {
                            
                            }
                        }).catch( function(response) {
                            console.log("add book..." + response);
                        });
                        this.$Message.success('Success!');
                    } else {
                        this.$Message.error('Fail!');
                    }
                })
            },
            handleReset (name) {
                this.$refs[name].resetFields();
            },
            //handle upload pic
            handleView (name) {
                this.imgName = name;
                this.visible = true;
            },
            handleRemove (file) {
                const fileList = this.$refs.upload.fileList;
                this.$refs.upload.fileList.splice(fileList.indexOf(file), 1);
            },
            handleSuccess (res, file) {
                file.url = '@/assets/img/1984.jpg';
                file.name = '1984.jpg';
            },
            handleFormatError (file) {
                this.$Notice.warning({
                    title: 'The file format is incorrect',
                    desc: 'File format of ' + file.name + ' is incorrect, please select jpg or png.'
                });
            },
            handleMaxSize (file) {
                this.$Notice.warning({
                    title: 'Exceeding file size limit',
                    desc: 'File  ' + file.name + ' is too large, no more than 2M.'
                });
            },
            handleBeforeUpload (file) {
                let reader = new FileReader()
                reader.readAsDataURL(file)
                const _this = this
                reader.onloadend = function (e) {
                    file.url = reader.result
                    _this.uploadList.push(file)
                }

                // const check = this.uploadList.length < 5;
                // if (!check) {
                //     this.$Notice.warning({
                //         title: 'Up to five pictures can be uploaded.'
                //     });
                // }
                // return check;
            }
        },
        mounted () {
            this.uploadList = this.$refs.upload.fileList;
        }
    }
</script>

