<template>
  <div class="vm-panel" :style="{ backgroundColor: bgColor }" :class="{ bgStyle: bgColor }">
    <div class="card-desc panel-body">
      <div v-if="editable && type == 'vertical'" class="control">
        <span class="edit" @click="modalEdit=true">
          <i class="fa fa-pencil"></i>
        </span>
        <span class="delete">
          <i class="fa fa-trash" @click="modalDelete=true"></i>
        </span>
        
      </div>
      <div v-if="title" class="panel-heading" :style="{ backgroundColor: hdColor }" :class="{ bgStyle: hdColor }">
        {{ title }}
      </div>
      <div class="panel-body">
        {{ desc }}
      </div>
      <!--<a :href="detailUrl">
        more >
      </a>-->
      
    </div>
    <Modal
        v-model="modalEdit"
        title="Edit"
        ok-text="OK"
        cancel-text="Cancel"
        v-on:on-ok="editOk">
        <VmFormNote></VmFormNote>
    </Modal>
    <Modal
        v-model="modalDelete"
        title="Delete"
        ok-text="OK"
        cancel-text="Cancel"
        v-on:on-ok="deleteOk">
        Are you sure to delete this data?
    </Modal>
  </div>
</template>
<script>
  import VmFormNote from '@/components/vm-form-note'
  export default {
    name: 'VmNoteCard',
    components: {
      VmFormNote
    },
    props: {
      type: {
        type: String,
        default: 'vertical'
      },
      editable: {
        type: Boolean,
        default: false
      },
      title: {
        type: String,
        default: 'Title'
      },
      desc: {
        type: String,
        default: 'Lorem Ipsum is simply dummy text of the printing and typesetting industry,Lorem Ipsum has been the industry\'s standard dummy text ever since the 1500s'
      },
      detailUrl: {
        type: String,
        default: '#'
      },
      editUrl: {
        type: String,
        default: '#'
      },
      addUrl: {
        type: String,
        default: '#'
      },
      bgColor: {
        type: String,
        default: ''
      },
      hdColor: {
        type: String,
        default: ''
      }
    },
    data: function () {
      return {
        deleteDisabled: true,
        dataShow: [],
        showNum: 10,
        showStripe: false,
        tableSize: 'default',
        currentPage: 1,
        keyword: '',
        modalEdit: false,
        modalDelete: false,
        dataEdit: {},
        dataDelete: [],
        dataAdd: {},
        formData: []
      }
    },
    methods: {
      editOk: function () {
        this.$emit('edit-ok', this.dataEdit)
      },
      deleteOk: function () {
        this.$emit('delete-ok')
      }
    }
  }
</script>

<style lang="less" scoped>
  .bgStyle{
    color: rgba(255,255,255,0.8);
    .panel-heading{
      border-bottom: 1px solid rgba(255,255,255,0.3);
    }
  }
  .hdStyle{
    color: rgba(255,255,255,0.8);
  }
</style>


